<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基础用法1">基础用法</a></li><li><a href="#vue-doc-手风琴1">手风琴</a></li><li><a href="#vue-doc-面板嵌套1">面板嵌套</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-collapse折叠面板1" class="vue-doc-element">Collapse 折叠面板</h1>
<p class="vue-doc-element">将内容区域折叠/展开。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-button1" class="vue-doc-element">Button</h1>
<p class="vue-doc-element">Basic component that is used to trigger bussiness logic.</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<demoaccordion></demoaccordion>
<demonest></demonest>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-collapseprops1_1" class="vue-doc-element">Collapse props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>当前激活的面板的 name，可以使用 v-model 双向绑定</td>
<td>Array / String</td>
<td>-</td>
</tr>
<tr>
<td>accordion</td>
<td>是否开启手风琴模式，开启后每次至多展开一个面板</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-collapseevents1_2" class="vue-doc-element">Collapse events</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>切换面板时触发，返回当前已展开的面板的 key，格式为数组</td>
<td>[]</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-panelprops1_3" class="vue-doc-element">Panel props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>当前面板的 name，与 Collapse的value对应，不填为索引值</td>
<td>String</td>
<td>index</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-panelslot1_4" class="vue-doc-element">Panel slot</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>无</td>
<td>面板头内容</td>
</tr>
<tr>
<td>content</td>
<td>描述内容</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th style="text-align:right">Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td style="text-align:right">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import demoaccordion from './demo/accordion.md';
import demonest from './demo/nest.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demoaccordion,demonest
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


